<template>
    <div class="container text-white">
        <h2 class="text-center mt-5">/</h2>

        <div class="row justify-content-center">

            <div class="col-6">
                <div class="form-group text-center">

                    <ImageMixer :originalImage="originalImage" />
                    <div class="mt-3">
                        <button type="button" class="btn btn-secondary" @click="decrementQuantity"
                            :disabled="quantity <= 1">-</button>
                        <span class="mx-3">: {{ quantity }}</span>
                        <button type="button" class="btn btn-secondary" @click="incrementQuantity">+</button>
                    </div>
                </div>
            </div>
            <div class="col-6">

                <div class="card-body">
                    <form @submit.prevent="submitForm">

                        <!--   -->
                        <div class="mt-5">
                            <h3 class="mt-3"> </h3>
                            <div class="form-group">
                                <label for="buyerName"></label>
                                <input type="text" class="form-control" id="buyerName" v-model="buyer.name" placeholder=""
                                    required>
                            </div>
                            <div class="form-group">
                                <label for="buyerEmail">Email</label>
                                <input type="email" class="form-control" id="buyerEmail" v-model="email" readonly>
                            </div>
                            <div class="form-group">
                                <label for="buyerPhone"></label>
                                <input type="tel" class="form-control" id="buyerPhone" v-model="buyer.phone"
                                    placeholder="010-xxxx-xxxx" required>
                            </div>
                        </div>
                        <!--   -->

                        <div class="mt-5">
                            <h3 class="my-3"> </h3>
                            <div class="form-group">
                                <label for="recipientName"></label>
                                <input type="text" class="form-control" id="recipientName" v-model="recipient.name"
                                    placeholder="" required>
                            </div>
                            <div class="form-group">
                                <label for="recipientAddress" id="postcode"> </label>
                                <div class="input-group">
                                    <input type="text" class="form-control" v-model="recipient.postcode" placeholder=""
                                        id="postcode" readonly required>
                                    <input type="button" class="btn btn-warning" @click="execDaumPostcode()"
                                        value=" ">
                                </div>
                            </div>

                            <div class="form-group">
                                <label for="recipientAddress"></label>
                                <input type="text" class="form-control" v-model="recipient.address" id="recipientAddress"
                                    placeholder="" required readonly>
                            </div>

                            <div class="form-group">
                                <label for="recipientAddress1"> </label>
                                <input type="text" class="form-control" id="recipientAddress1" placeholder="" required>
                            </div>

                            <div class="form-group">
                                <label for="recipientPhone"></label>
                                <input type="tel" class="form-control" id="recipientPhone" v-model="recipient.phone"
                                    placeholder="010-xxxx-xxxx" required>
                            </div>
                            <div class="form-group">
                                <label for="buyerInfo"> </label>
                                <input type="tel" class="form-control" v-model="buyer.info" id="buyerInfo"
                                    placeholder=" ">
                            </div>
                        </div>

                        <!--   -->
                        <div class="mt-5">
                            <h3 class="my-3"> </h3>
                            <div class="form-group">
                                <label for="totalPrice">  </label>

                                <div class="input-group">
                                    <input type="number" class="form-control" id="totalPrice" :value="totalPrice" readonly>
                                    <span class="btn btn-warning"></span>
                                </div>

                            </div>
                            <!--   -->
                            <div class="mt-5 mb-5">
                                <h3 class="my-3"></h3>
                                <div class="text-align-center">
                                    <button class="custom-button">
                                        <img src="../assets/payment.png" />
                                    </button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import ImageMixer from '../components/ImageMixer.vue';

export default {
    components: {
        ImageMixer,
    },
    data() {
        return {
            buyer: {
                name: '',
                phone: ''
            },
            recipient: {
                name: '',
                postcode: '',
                address: '',
                extraAddress: '',
                info: '',
            },
            payment: {
                totalPrice: 0,
            },
            quantity: 1
        };
    },
    computed: {
        ...mapState({
            email: state => state.email,
            originalImage: state => state.image.originalImage
        }),
        totalPrice() {
            return this.quantity * 22000;  //  * 
        },
    },
    methods: {
        incrementQuantity() {
            this.quantity++;
        },
        decrementQuantity() {
            if (this.quantity > 1) {
                this.quantity--;
            }
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.recipient.extraAddress !== "") {
                        this.recipient.extraAddress = "";
                    }
                    if (data.userSelectedType === "R") {
                        //     
                        this.recipient.address = data.roadAddress;
                    } else {
                        //     (J)
                        this.recipient.address = data.jibunAddress;
                    }

                    //       .
                    if (data.userSelectedType === "R") {
                        //    . ( )
                        //     "//" .
                        if (data.bname !== "" && /[||]$/g.test(data.bname)) {
                            this.recipient.extraAddress += data.bname;
                        }
                        //  ,   .
                        if (data.buildingName !== "" && data.apartment === "Y") {
                            this.recipient.extraAddress +=
                                this.recipient.extraAddress !== ""
                                    ? `, ${data.buildingName}`
                                    : data.buildingName;
                        }
                        //    ,     .
                        if (this.recipient.extraAddress !== "") {
                            this.recipient.extraAddress = `(${this.recipient.extraAddress})`;
                        }
                    } else {
                        this.recipient.extraAddress = "";
                    }
                    //  .
                    this.recipient.postcode = data.zonecode;
                },
            }).open();
        },
        submitForm() {
        }
    }
};
</script>

<style scoped>
.custom-button {
    border: none;
    background: none;
}

.custom-button img {
    width: 100px;
    height: auto;
}

.text-align-center {
    text-align: center;
}

.form-group {
    margin-top: 20px;
}
</style>
